<template>
<div class="tr clearfix">
    <span class="box-twelve" v-for="i in 12"
          @click="chooseMonth(i)">{{i | chinesize}}</span>
</div>
</template>
<script>
import Bus from '../Bus.js'
import '../../filters/index.js'
export default {
  props: ['date'],
  methods: {
    chooseMonth (val) {
      // Bus.$emit('changeMonth', val)
      this.$emit('change-date', {mm: val}, 10)
    },
    toPrev () {
      this.$emit('change-date', {yy: --this.date.yy})
    },
    toNext () {
      this.$emit('change-date', {yy: ++this.date.yy})
    }
  }
}
</script>
<style scoped>
.clearfix:before{display: table; clear: both; content: '';}
.table{}
.table .caption{}
.table .tr span{width: 62px;}
.table .td{display: inline-block; width: 32px; min-height: 1px; float: left; text-align: right;}
.table .td.hasWritten{color: #f00;}

</style>


